<template>
  <div>
    <!-- 头部标题 -->
    <div class="header">公告管理</div>
    <!-- 内容区域 -->
    <div class="container">
      <!-- 搜搜区域 -->
      <div class="searchBox">
        <input
          class="searchInput"
          v-model="search.keyword"
          type="text"
          placeholder="请输入查找内容"
        />

        <button type="button" class="searchBtn" @click="searchNotice">
          搜索
        </button>
        <button type="button" class="addBtn" @click="addNotice">
          发布公告
        </button>
      </div>
      <!-- 展示列表区 -->
      <div class="listBox">
        <!-- 轮播图列表 -->
        <div class="list">
          <template>
            <el-table :data="tableData" border stripe style="width: 100%">
              <el-table-column
                prop="nid"
                label="公告ID"
                width="120"
                align="center"
              >
              </el-table-column>
              <el-table-column
                prop="title"
                label="公告标题"
                width="300"
                align="center"
              >
              </el-table-column>
              <el-table-column
                prop="noticeDate"
                label="修改日期"
                width="150"
                align="center"
              >
              </el-table-column>
              <el-table-column label="操作" align="center">
                <template v-slot="{ row }">
                  <el-button
                    size="mini"
                    type="primary"
                    @click="updateNotice(row)"
                    >编辑查看
                  </el-button>
                  <el-button
                    size="mini"
                    type="danger"
                    @click="deleteNotice(row.nid)"
                    >删除公告</el-button
                  >
                </template>
              </el-table-column>
            </el-table>
          </template>
        </div>
        <!-- 分页器 -->
        <div class="pager">
          <!-- 分页区域 -->
          <el-pagination
            @current-change="handleCurrentChangeFn"
            :current-page.sync="search.pagenum"
            :page-size.sync="search.pageSize"
            layout="total, prev, pager, next, jumper"
            :total="total"
          >
          </el-pagination>
        </div>
      </div>
    </div>
    <!-- 表单dialog -->
    <el-dialog
      :title="isEdit ? '编辑公告' : '发布公告'"
      :visible.sync="dialogFormVisible"
    >
      <el-form :model="form">
        <!-- 菜品ID -->
        <el-form-item
          label="公告ID"
          v-show="form.nid"
          :label-width="formLabelWidth"
        >
          <el-input disabled v-model="form.nid" autocomplete="off"></el-input>
        </el-form-item>
        <!-- 发布时间 -->
        <el-form-item
          label="发布时间"
          v-show="form.noticeDate"
          :label-width="formLabelWidth"
        >
          <el-input
            v-model="form.noticeDate"
            disabled
            autocomplete="off"
          ></el-input>
        </el-form-item>
        <!-- 公告标题 -->
        <el-form-item label="公告标题" :label-width="formLabelWidth">
          <el-input v-model="form.title" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="公告详情" :label-width="formLabelWidth">
          <el-input
            type="textarea"
            placeholder="请输入内容"
            v-model="form.content"
            maxlength="300"
            :autosize="{ minRows: 4, maxRows: 3 }"
            show-word-limit
          >
          </el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="sureBtn">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {
  getNoticeListAPI,
  addNoticeAPI,
  updateNoticeAPI,
  deleteNoticeAPI,
} from "@/api";
export default {
  name: "noticeCMD",
  // ==========================================以下是data==========================================
  data() {
    return {
      search: {
        pagenum: 1,
        pageSize: 6,
        keyword: "",
      },
      // 判断是否处于编辑
      isEdit: false,
      // 总共的轮播图数
      total: 100,
      // 用于控制dialogform
      dialogFormVisible: false,
      // 用于控制表单label的宽
      formLabelWidth: "120px",
      // 用于收集表单的数据
      form: {
        nid: "",
        title: "",
        noticeDate: "",
        content: "",
      },
      tableData: [],
    };
  },
  // ==========================================以下是data==========================================
  methods: {
    // 添加公告
    addNotice() {
      this.isEdit = false;
      this.dialogFormVisible = true;
      this.clearnForm();
    },
    async addNoticeFn() {
      const { data: res } = await addNoticeAPI(this.form);
      if (res.code != 0) return this.$message.error("添加公告失败！");
      this.$message.success("添加公告成功！");
      this.getNoticeList();
      this.dialogFormVisible = false;
    },
    // 编辑公告
    updateNotice(notice) {
      this.isEdit = true;
      this.form = { ...notice };
      this.dialogFormVisible = true;
    },
    async updateNoticeFn() {
      const { data: res } = await updateNoticeAPI(this.form);
      if (res.code != 0) return this.$message.error("公告编辑失败！");
      this.$message.success("公告编辑成功！");
      this.getNoticeList();
      this.dialogFormVisible = false;
    },
    // 删除公告
    async deleteNotice(noticeID) {
      // 1. 询问用户是否要删除
      const confirmResult = await this.$confirm(
        "是否确认删除公告！！",
        "提示",
        {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
        }
      ).catch((err) => err);

      // 2. 取消了删除
      if (confirmResult === "cancel") return;
      const { data: res } = await deleteNoticeAPI({ id: noticeID });
      if (res.code != 0) return this.$message.error("删除公告失败！");
      this.$message.success("删除成功！");
      this.search.pagenum = 1;
      this.getNoticeList();
    },
    async getNoticeList() {
      const { data: res } = await getNoticeListAPI({
        page: this.search.pagenum,
        title: this.search.keyword,
      });
      console.log(res);
      if (res.code != 0) return this.$message.error("获取公告列表失败！");
      this.total = res.count;
      this.tableData = res.data;
    },
    searchNotice() {
      if (this.search.keyword == "") {
        this.$message("请输入搜索关键词！");
        this.search.pagenum = 1;
        return this.getNoticeList();
      }
      this.search.pagenum = 1;
      this.getNoticeList();
    },
    // 页码值发生了变化
    handleCurrentChangeFn(newPage) {
      // 为页码值赋值
      this.search.pagenum = newPage;
      // 重新发起请求
      this.getNoticeList();
    },
    // 清除信息
    clearnForm() {
      this.form.nid = "";
      this.form.title = "";
      this.form.noticedate = "";
      this.form.content = "";
    },
    // 表单确认按钮
    sureBtn() {
      if (this.isEdit) return this.updateNoticeFn();
      return this.addNoticeFn();
    },
  },
  // ==========================================以下是mounted==========================================
  mounted() {
    this.getNoticeList();
  },
};
</script>

<style scoped>
.header {
  height: 60px;
  width: 80%;
  margin: 0 auto;
  text-align: center;
  line-height: 60px;
  font-size: 40px;
  font-family: "幼圆";
  font-weight: 600;
  color: rgb(84, 87, 87);
  background-color: rgb(197, 199, 205);
  border: rgb(183, 184, 187) solid 10px;
  border-radius: 10px;
}
.container {
  width: 90%;
  height: 485px;
  background-color: rgb(186, 193, 193);
  margin: 10px auto;
  border-radius: 10px;
  overflow: hidden;
}
.searchBox {
  width: 65%;
  height: 40px;
  margin: 10px auto;
  vertical-align: middle;
  overflow: hidden;
}
.searchInput {
  width: 70%;
  height: 30px;
  font-size: 15px;
  border-radius: 6px;
  border: rgb(242, 240, 210) solid 1px;
}
.searchBtn {
  width: 60px;
  height: 30px;
  font-size: 15px;
  color: rgb(67, 70, 72);
  background-color: rgb(169, 193, 225);
  border: rgb(127, 171, 228) solid 2px;
  border-radius: 6px;
  cursor: pointer;
  margin-left: 10px;
}
.searchBtn:active {
  background-color: rgb(248, 248, 248);
  border: rgb(232, 233, 235) solid 2px;
}
.addBtn {
  width: 120px;
  height: 30px;
  font-size: 15px;
  color: rgb(67, 70, 72);
  background-color: rgb(239, 176, 76);
  border: rgb(226, 192, 99) solid 2px;
  border-radius: 6px;
  cursor: pointer;
  margin-left: 10px;
}
.addBtn:active {
  background-color: rgb(248, 242, 124);
  border: rgb(216, 229, 77) solid 2px;
}
.listBox {
  width: 80%;
  height: 400px;
  background-color: rgb(244, 244, 245);
  border-radius: 5px;
  margin: auto;
  overflow: hidden;
}
.list {
  width: 98%;
  height: 360px;
  margin: 8px auto;
  overflow: hidden;
}
.pager {
  width: 65%;
  height: 30px;
  margin: auto;
  margin-top: -8px;
}
</style>
